.page-wrap {
    width: 100%;
    /* height: 1000px; */
    display: grid;
    justify-items: center;
    align-items: center;
    background-color: whitesmoke;
    padding: 50px 0;
    grid-gap: 10px;
}

.content-2 {
    width: 100%;
    /* height: 400px; */
    display: grid;
    grid-gap: 10px;
    /*margin: 10px;*/
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
}

.content-3 {
    width: 100%;
    /* height: 300px; */
    display: grid;
    grid-gap: 10px;
    grid-template-columns: repeat(3, 1fr);
    align-items: center;
}

.content-4 {
    width: 100%;
    /* height: 300px; */
    display: grid;
    grid-gap: 10px;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
}

.text-2-b,
.text-2-w,
.text-3-w,
.text-3-b {
    /* width: 100%; */
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px;
    box-sizing: border-box;
}

.text-2-b,
.text-3-b {
    background-color: gainsboro;
    color: black;
}

.text-2-w,
.text-3-w {
    background-color: black;
    color: white;
}

.content-4 .text-3-w, .content-4 .text-3-b {
    height: 400px;
}

.text-2-w h2,
.text-2-b h2,
.text-3-w h3,
.text-3-b h3 {
    font-weight: 600;
    font-size: 3.3vw;
    margin-bottom: 20px;
}

.text-3-w h3,
.text-3-b h3 {
    font-size: 2.5vw;
}

.text-2-w p,
.text-2-b p,
.text-3-w p,
.text-3-b p {
    font-size: 1vw;
    margin: 3px 0;
}

.img-2,
.img-3,
.img-4 {
    display: flex;
    height: 400px;
    box-sizing: border-box;
}

.img-2 img, .img-3 img, .img-4 img{
    height: 400px;
    width: 100%;
    object-fit: cover;
}



@media (max-width: 576px) {

    .content-2, .content-3, .content-4 {
        display: grid;
        grid-template-columns: 1fr;
        grid-gap: 10px;
    }

    .text-2-w,
    .text-2-b,
    .text-3-b,
    .text-3-w,
    .img-2, .img-3 {
        height: 300px;
    }

    .text-2-w h2,
    .text-2-b h2,
    .text-3-w h2,
    .text-3-b h2 {
        font-size: 8vw;
    }

    .text-3-w h3,
    .text-3-b h3 {
        font-size: 6vw;
    }

    .text-2-w p,
    .text-2-b p,
    .text-3-w p,
    .text-3-b p {
        font-size: 3vw;
    }

    .img-2, .img-3, .img-4 {
        display:grid;
        min-height: 300px;
        box-sizing:border-box;
    }

    .img-2 img, .img-3 img, .img-4 img {
        max-height: 300px;
    }

    .content-4 .text-3-w, .content-4 .text-3-b {
        height: 270px;
    }


}

