.main-background {
    width: 100%;
    height: 1000px;
    background-color: black;
    padding: 1px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .logo {
    width: 60vw;
    max-width: 1000px;
    min-width: 150px;
    height: 100vw;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    position: absolute;
    margin-bottom: 100px;
  }
  
  .main-title {
    color: white;
    font-size: 4vw;
    text-align: center;
    margin-top: 20vw;
  }
  