.gallery-container {
    height : 1000px;
    display : flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 20%;
  }

.gallery {
    display:flex;
    flex-direction: column;
    height: 95%;
    width: 100%;
  }

.galleryText{
    margin-left: 5%;
  }

.gallery-main-img {
    /* display: flex; */
    height: 60%;
    width: 90%;
    margin: 1% 5%;
  }
  
  .gallery-main-img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .gallery-imglist {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-gap: 10px;
    margin: 10px 5%;
    height: 30%;
  }
  
  .gallery-imglist img{
    width: 100%;
    height: 100%;
    cursor: pointer;
    object-fit: cover;
  }

@media (max-width : 1200px) {

  .gallery-main-img {
      height: 45%;
      width: 90%;
      margin: 1% 5%;
  }

  .gallery-imglist {
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(3, 1fr);
    grid-gap: 10px;
    margin: 10px 5%;
    height: 30%;
  }
}

@media (max-width : 576px) {

  .gallery-main-img {
    /* display: flex; */
    height: 40%;
    width: 90%;
    margin: 1% 5%;
  }

  .gallery-imglist {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(4, 1fr);
    grid-gap: 10px;
    margin: 10px 5%;
    height: 30%;
  }

}