
.intro-container {
    height : 1000px;
    display : flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 10%;
  }

.intro {
    display: grid;
    margin-top:100px;
    height: 95%;
    width: 100%;
    grid-gap: 10px;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(7, 1fr);
    grid-template-areas:
        "a b"
        "a b"
        "a b"
        "c b"
        "c d"
        "c d"
        "c d";
}


.intro-img1 {
    grid-area: b;
}

.intro-img2 {
    grid-area: c;
}

.intro-img1 img,
.intro-img2 img {
    height: 100%;
    width: 100%;
    object-fit: cover;
}

.intro-text1 {
    font-size: 16px;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    grid-area: a;
    background-color: gainsboro;
    color: black;
}

.intro-text2 li {
    font-weight: 550;
    font-size: 120%;
    margin-bottom: 1.2vw;
    margin-left: 35%;
    text-decoration: underline;
}

.intro-text2 {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    grid-area: d;
    background-color: black;
    color: white;
}

nav ul {
    list-style: none;
    padding: 0;

}

nav ul li a {
    color: white;
}

.intro-text1 p {
    margin: 0.3vw;
}


@media (max-width : 1200px) {
    .intro-text1 {
        font-size: 15px;
    }
    .intro-text2 li {
        margin-left: 20%;
    }
}

@media (max-width : 576px) {
    .intro-text1 {
        font-size: 11px;
    }
    .intro-text2 li {
        margin-left: 20%;
    }
}