.map-container {
    height : 1000px;
    display : flex;
    flex-direction: column;
    justify-content: center;
    margin-bottom: 10px;
  }
  
.mapPage {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    grid-template-areas: "a a a a a b b";
    grid-gap: 2%;
}

.mapcontainer {
    grid-area: a;
}

.store-list {
    grid-area: b;
    overflow-y: auto;
    background: white;
    padding: 10px;
    border: 1px solid #ccc;
    box-sizing: border-box;
}

.store {
    height: 12vh;
    padding: 10px;
    cursor: pointer;      
}

.store:hover {
    background-color: #f0f0f0;
}

.store-list h3{
    font-size: 2vw;
    font-weight: 900;
}