
.brand-container {
    height : 1000px;
    display : flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 10%;
  }


.brand {
    display: grid;
    /* margin-top: 100px; */
    height: 95%;
    width: 100%;
    grid-gap: 10px;
    grid-template-columns: repeat(6, 1fr);
    grid-template-areas: "aa bb bb cc cc dd"
}

.brand-img1 {
    grid-area: aa;
    /* height: 90%;
    width: 100%;
    object-fit: cover; */
}

.brand-img2 {
    grid-area: dd;
    /* height: 90%;
    width: 100%;
    object-fit: cover; */
}

.brand-img1 img,
.brand-img2 img {
    height: 90%;
    width: 100%;
    object-fit: cover;
}

.brand-text1 {
    grid-area: bb;
    height: 90%;
    width: 100%;
    background-color: gainsboro;
    color: black;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.brand-text1-orange {
    font-size : 3vw;
    font-weight: 700;
    display: inline-block;
    color: orangered;
    margin: 0;
}

.brand-text1-black {
    font-size : 3vw;
    font-weight: 500;
    display: inline-block;
    color: black;
    margin: 0;
}

.brand-text2 {
    grid-area: cc;
    height: 90%;
    background-color: black;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.brand-text2 p {
    margin: 20px;
    margin-left: 15%;
    margin-right: 15%;
}



@media (max-width : 1200px) {
    .brand-text1-orange,     .brand-text1-black {
        font-size: 3vw;
        margin: 0;
    }

    .brand-text2 {
        font-size : 1.5vw;
    }

}

@media (max-width : 576px) {


    .brand-text1-orange,     .brand-text1-black {
        font-size: 4vw;
        margin: 0;
    }

    .brand-text2 {
        font-size : 2vw;
    }

}